import { Typography } from '@mui/material';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { connect } from 'react-redux';

function Item(props){

  let b = 0;
    return(
      <>
      <Box sx={{ display: 'flex',
                 flexDirection: 'column',
                 alignItems: 'center'}}>
      <TableContainer>
        <Table sx={{ Width: 700 }} >
          <TableHead>
            <TableRow>
              <TableCell align="center">#</TableCell>
              <TableCell align="center">상품명</TableCell>
              <TableCell align="center">수량</TableCell>
              <TableCell align="center">변경</TableCell>
              <TableCell align="center">가격</TableCell>
            </TableRow>
          </TableHead>
          { 
            props.cart.map((a,i)=>{
              let c = a.price*a.quan;
              let d = c + 0;
              return (
          <TableBody key={i}>
                <TableCell align="center"><img width='50' src={(a.img)}/></TableCell>
                <TableCell align="center">{a.title}</TableCell>
                <TableCell align="center">{a.quan}</TableCell>
                <TableCell align="center"><Button onClick={()=>{ props.setCount(props.count + 1); props.dispatch({type: 'plus', payload : a.title})  }}>+</Button><Button onClick={()=>{props.setCount(props.count - 1); props.dispatch({type: 'minus', payload : a.title }) }}>-</Button></TableCell>
                <TableCell align="center">{a.price*a.quan}</TableCell>
          </TableBody>
              )})
          }
        </Table>
        <Typography sx={{mt:2, mr: 7}}align="right">{(props.d)}</Typography>
      </TableContainer>
      </Box>
      </>
    )
  }

  function UseRedux(state){
    return {
      cookielist : state.reducer,
      cart : state.reducer2,
      cakelist : state.reducer3,
      event : state.reducer4
    }
  }
  
  export default connect(UseRedux)(Item);