let now = new Date();
let year = now.getFullYear();
let Month = now.getMonth() + 1;
let Day = now.getDate();
if (Month < 10) { Month = "0" + Month; } if (Day < 10) { Day = "0" + Day; }


const postList = [
  {
    "no": 1,
    "title": "배송관련 안내문 입니다",
    "content": "케이크를 제외한 전 품목 배송 가능하며, 배송비는 3000원입니다.",
    "createDate": (year)+"-"+(Month)+"-"+(Day),
  },
  {
    "no": 2,
    "title": "크리스마스 이벤트 입니다",
    "content": "크리스마스 한정 케이크를 주문 제작 받고 있습니다, 사진은 @@@로 보내주시면 됩니다",
    "createDate": (year)+"-"+(Month)+"-"+(Day),
  },
  {
    "no": 3,
    "title": "세번째 게시글입니다.",
    "content": "세번째 게시글 내용입니다.",
    "createDate": (year)+"-"+(Month)+"-"+(Day),
  },
  {
    "no": 4,
    "title": "네번째 게시글입니다.",
    "content": "네번째 게시글 내용입니다.",
    "createDate": (year)+"-"+(Month)+"-"+(Day),
  },
];

const getPostByNo = no => {
  const array = postList.filter(x => x.no == no);
  if (array.length == 1) {
    return array[0];
  }
  return null;
}

export {
  postList,
  getPostByNo
};