import React from 'react';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { connect } from 'react-redux';
import { useParams, useHistory } from 'react-router-dom';


function Detail(props){
  let history = useHistory();
    let { id, item } = useParams();
    return(
      <>
            <Box
        sx={{
          display: 'flex',
          flexDirection: 'row',
          alignItems: 'center',
          justifyContent: "center",
          '& > *': { mt: 3},}}>
        <img width='300' height='300' src={props[item][id].img}></img>
  
        <Stack
            className="AddS"
            sx={{display: 'grid',
            ml: 8,
            gap: 3,
            gridTemplateColumns: 'repeat(1, 1fr)'}}
            direction="row"
            justifyContent="center"
          >
        <Typography sx={{display: 'flex', flexDirection: 'column', alignItems: 'center' }} variant="h4" >{props[item][id].title}</Typography>
        <Typography sx={{ml:7}} variant="h4">{props[item][id].price}원 </Typography>
        <Stack direction="row" spacing={2} >
        <Button onClick={()=>{ props.setCount(props.count + 1);
                props.dispatch({type : 'addcart', payload : {id:(props[item][id].id) , img: (props[item][id].img), title:(props[item][id].title), quan: 1, price:(props[item][id].price) }});history.push('/item'); }} sx={{ml:2}} color="success" variant="contained">구매하기</Button>
        <Button  onClick={() => {
                props.setCount(props.count + 1);
                props.dispatch({type : 'addcart', payload : {id:(props[item][id].id) , img: (props[item][id].img), title:(props[item][id].title), quan: 1, price:(props[item][id].price) }});
              }} color="success" variant="contained">장바구니</Button>
        </Stack>
          </Stack>
  
  
      </Box>
    </>
    )
  }

  function UseRedux(state){
    return {
      cookielist : state.reducer,
      cart : state.reducer2,
      cakelist : state.reducer3,
      event : state.reducer4
    }
  }
  
  export default connect(UseRedux)(Detail);