let cart = [];

function reducer2(state = cart, action){
  if( action.type === 'addcart' ){
  let found = state.findIndex((a)=>{return a.title === action.payload.title});

  if ( found >= 0 ){
    let copy = [...state];
    copy[found].quan++;
    return copy
  }
  else {
    let copy = [...state];
    copy.push(action.payload);
    return copy
  }
 }

 else if (action.type === "plus") {
      let copy = [...state];
      let found = state.findIndex((a) => {
        return a.title === action.payload;
      });
      copy[found].quan++;
      return copy;
    } else if (action.type === "minus") {
      let copy = [...state];
      let found = state.findIndex((a) => {
        return a.title === action.payload;
      });
      if (copy[found].quan <= 0) {
        copy[found].quan = 0;
      } else {
        copy[found].quan--;
      }
      return copy;
  } else{
     return state
  }
 }



